import React, { ChangeEvent, FC, useEffect, useState } from 'react'
import clsx from 'clsx'
import { Condition } from '../../other/Condition'
import { CheckboxProps } from './types'
import './Checkbox.style.scss'
import { CheckboxMarkIcon } from '../../icons/CheckboxMark'

const Checkbox: FC<CheckboxProps> = ({
  id,
  disabled,
  name,
  label,
  labelJsx,
  className,
  size = 'L',
  isCheckedOuter = true, // Gives the ability to change checkbox's state from outside by sending this prop
  initChecked = false,
  onChange,
}) => {
  const [checked, setChecked] = useState(initChecked)

  const handleChange = (e: ChangeEvent<HTMLInputElement>): void => {
    onChange(e.target.checked)
    setChecked((value) => !value)
  }

  useEffect(() => {
    setChecked(initChecked)
  }, [initChecked])

  const getColor = (disabled, checked): string => {
    if (disabled && checked) {
      return '#8E979F'
    }
    if (disabled) {
      return '#D9DCDE'
    }
    return '#FFFFFF'
  }

  useEffect(() => {
    if (isCheckedOuter != null) {
      setChecked(isCheckedOuter)
    }
  }, [isCheckedOuter])

  return (
    <div className={clsx('CheckboxNext', disabled && '--disabled', className)}>
      <label className='CheckboxNext__Label' htmlFor={id}>
        <input
          checked={checked}
          className='CheckboxNext__Input'
          disabled={disabled}
          id={id}
          name={name}
          onChange={handleChange}
          type='checkbox'
        />
        <div
          className={clsx(
            'CheckboxNext__CustomInput',
            `CheckboxNext__CustomInput__Size_${size}`,
          )}
        >
          <CheckboxMarkIcon
            className={`CheckboxNext__CustomInput__Size_${size}-icon`}
            fill={getColor(disabled, checked)}
          />
        </div>
        <div className={`CheckboxNext__CustomInput__Size_${size}-text`}>
          <Condition match={Boolean(labelJsx)}>{labelJsx}</Condition>
          <Condition match={!labelJsx && Boolean(label)}>
            <p className={clsx(`CheckboxNext__CustomInput__Size_${size}-text`)}>
              {label}
            </p>
          </Condition>
        </div>
      </label>
    </div>
  )
}

export default Checkbox
